.card-link {
    text-decoration: none;
    .result-card {
        display: flex;
        justify-content: center;
        color: #e7e9f0;
        background-color: #282d4f;
        margin: 10px 15px;
        min-width: 75px;
        padding: 10px 20px;
        border-radius: 10px;
        box-shadow: 0 0 5px rgba(255,255,255,0.1);
        transition: background 0.35s;
        &:hover {
            background-color: #4a5079;
            box-shadow: 0 0 5px rgba(255,255,255,0.5);
        }
        p {
            margin: 0;
            font-weight: 100;
        }
    }
}
